.news .headline {
    .leads {
        grid-column: 2 / span 3;
        .left {
           grid-column: 1 / span 2 ; }
        .right {
            grid-column: 4 / span 8; } }
    .feats {
        .flex-row {
            width: 100%; }
        .meta {
            width: 100%; }
        .thumb {
            width: 90px;
            height: 90px;
            img {
                aspect-ratio: 1 / 1; } } } }

